.form {
    display: flex;
    padding: 20px;
}

.input {
    width: 300px;
    height: 30px;
    margin-right: 20px;
    padding-left: 10px;
}

.btnForm {
    border-radius: 3px;
    width: 80px;
    height: 35px;
}

.btnForm:hover, :focus {
    background-color: deepskyblue;
    color: white;
    cursor: pointer;
}